.settings{
    display: flex;
}

.settingsWrapper{
    flex:9;
    padding: 20px;
}

.profileImg{
   width: 70px;height: 70px;
   border-radius: 20px;
   object-fit: cover;
}

.settingsTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingsUpdateTitle{
    font-size: 30px;
    margin-bottom: 20px;
    color:lightcoral
}

.settingsDeleteTitle{
    color:red;
    font-size: 12px;
    cursor: pointer;
}

.settingsForm{
    display: flex;
    flex-direction: column;

}

.settingsPP{
display: flex;
align-items: center;
margin-top: 10px 0;

}

.settingsPPIcon{
    width: 25px;height: 25px;
    border-radius:50px;
    background-color:lightcoral;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
}
.settingsForm>label{
    font-size: 20px;
    margin-top: 20px;


}

.settingsForm>input{
    color:gray;
    margin: 10px 0;
    height: 30px;
    border:none;
    border-bottom: 1px solid lightgray;
}

.settingsSubmit{
    width: 150px;
    border: none;
    border-radius: 10px;
    color:white;
    background-color:teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}
.settingsLogout{
    width: 120px;
    border: none;
    border-radius: 10px;
    color:white;
    font-weight: bold;
    background-color:red;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}
.logoutButton{
    display: flex;
    justify-content: flex-end;
}